<template>
  <div
    class="bannerBox boxBorder"
    :class="active"
    @mouseover="mouseOver"
    @mouseleave="mouseLeave"
    :style="{background:itemData.backgroundColor}">
    <!-- //0是轮播广告 1一行一个
          bannerType: 0,
          // 广告样式
          advertisingType: 0,
          //卡片倒角  0是直角 1圆角
          chamfering: 0,
          chamferingStyle: "",
          //卡片投影  0无投影 1有投影
          projection: 0,
          projectionStyle: "",
          //图片填充  0是填充 1周边留白
          fill: 0,
          fillStyle: "",
          //图片比例  0是2:1 1是4：3
          ratio: 0,
    ratioStyle: "",-->
    <!-- 轮播广告 -->
    <div class="type2" v-if="itemData.bannerType==0">
      <!-- 样式1 -->
      <div
        class="type1"
        :style="{padding:itemData.fillStyle,height:itemData.ratio==1?'269px':'188px'}"
        v-show="itemData.advertisingType==0&&itemData.bannerBg.length==0">
        <p class="typeBar" :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"></p>
      </div>
      <!-- 数据回显 -->

      <!-- 一行一个回显 -->
      <el-carousel
        :style="{padding:itemData.fillStyle,height:itemData.ratio==1?'269px':'188px'}"
        v-show="itemData.advertisingType==0&&itemData.bannerBg.length!=0"
        arrow="never">
        <el-carousel-item
          v-for="(item,index) in itemData.bannerBg"
          :key="index"
          :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"
        >
          <img :src="item.imageUrl" alt />
        </el-carousel-item>
      </el-carousel>

      <!-- 样式2 -->
      <ul
        :style="{padding:itemData.fillStyle,height:itemData.ratio==1?'258px':'180px'}"
        v-show="itemData.advertisingType==1&&itemData.bannerBg.length==0">
        <li :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"></li>
        <li :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"></li>
      </ul>

      <!-- 一行一个回显 -->
      <ul
        :style="{padding:itemData.fillStyle,height:itemData.ratio==1?'258px':'180px'}"
        v-show="itemData.advertisingType==1&&itemData.bannerBg.length!=0">
        <li
          :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle,border:'none'}"
          v-for="(item,index) in itemData.bannerBg"
          :key="index"
        >
          <img
            :src="item.imageUrl"
            alt
            :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"
          />
        </li>
      </ul>

      <!-- 样式3 -->
      <div
        class="type1"
        :style="{padding:itemData.fillStyle,height:itemData.ratio==1?'269px':'188px'}"
        v-show="itemData.advertisingType==2&&itemData.bannerBg.length==0">
        <p
          class="typeBar"
          :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"
        ></p>
        <i class="el-icon-arrow-left"></i>
        <i class="el-icon-arrow-right"></i>
      </div>
      <!-- 一行一个回显 -->
      <el-carousel
        :style="{padding:itemData.fillStyle,height:itemData.ratio==1?'258px':'180px',borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"
        v-show="itemData.advertisingType==2&&itemData.bannerBg.length!=0"
        indicator-position="none"
        arrow="always">
        <el-carousel-item
          v-for="(item,index) in itemData.bannerBg"
          :key="index"
          :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"
        >
          <img :src="item.imageUrl" alt />
        </el-carousel-item>
      </el-carousel>

      <!-- 样式4 -->
      <div
        class="type1"
        :style="{padding:itemData.fillStyle,height:itemData.ratio==1?'269px':'188px'}"
        v-show="itemData.advertisingType==3&&itemData.bannerBg.length==0">
        <p
          class="typeBar style2"
          :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"
        ></p>
        <p class="lbbtn">1/2</p>
      </div>
      <!-- 一行一个回显 -->
      <el-carousel
        :style="{padding:itemData.fillStyle,height:itemData.ratio==1?'258px':'180px',borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"
        v-show="itemData.advertisingType==3&&itemData.bannerBg.length!=0"
        indicator-position="none"
        arrow="never"
        @change="bgchange">
        <el-carousel-item
          v-for="(item,index) in itemData.bannerBg"
          :key="index"
          :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"
        >
          <img :src="item.imageUrl" alt />
        </el-carousel-item>
        <p class="lbbtn">{{gbindex}} / {{itemData.bannerBg.length}}</p>
      </el-carousel>
    </div>

    <!-- 一行一个 -->
    <div v-if="itemData.bannerType==1">
      <div
        class="type1"
        :class="itemData.fill ? type1Padding : '' "
        v-if="itemData.bannerBg.length==0"
        :style="{height:itemData.ratio==1?'282px':'197px'}">
        <p class="typeBar" :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"></p>
      </div>

      <!-- 一行一个回显 -->
      <div
        class="type1"
        :class="itemData.fill ? 'type1Padding' : 'type1Margin' "
        v-show="itemData.bannerBg.length!=0"
        :style="{height:itemData.ratio==1?'282px':'197px'}"
        v-for="(item,index) in itemData.bannerBg"
        :key="index">
        <div
          class="typeBar"
          :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle,border:'none'}">
          <img
            :src="item.imageUrl"
            alt
            :style="{borderRadius:itemData.chamferingStyle,boxShadow:itemData.projectionStyle}"
          />
        </div>
      </div>
    </div>

    <!-- 左侧介绍 -->
    <span class="box-title">{{itemData.name}}</span>
    <i class="delete-btn" v-if="deleteBtn" @click.stop="deleteBox(itemData)">
      <img src="@/assets/decorateBox/deleteBox.png" alt />
    </i>
  </div>
</template>

<script>
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      active: "", //绑定样式
      deleteBtn: false,
      gbindex: 1
    };
  },
  created(){
    console.log('图片广告数据回显',this.itemData)
  },
  methods: {
    // 移入事件
    mouseOver() {
      this.active = "active";
      this.deleteBtn = true;
    },
    // 移出事件
    mouseLeave() {
      this.active = "";
      this.deleteBtn = false;
    },
    //删除当前组件盒子事件
    deleteBox(box) {
      this.$emit("deleteBox", box);
    },
    bgchange(e) {
      this.gbindex = e + 1;
    }
  }
};
</script>

<style scoped lang="less">
.delete-btn {
  position: absolute;
  width: 32px;
  height: 32px;
  cursor: pointer;
  z-index: 3;
  top: 10px;
  right: -32px;
}
.box-title {
  position: absolute;
  cursor: pointer;
  z-index: 3;
  top: 0px;
  left: -70px;
  height: 24px;
  font-size: 12px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 2px 2px 4px 0px rgba(80, 80, 80, 0.2);
  color: rgba(102, 102, 102, 1);
  line-height: 24px;
  width: 64px;
  text-align: center;
  border-radius: 2px;
}
.box-title:after,
.box-title:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: 4px solid transparent;
  left: 50%;
  margin-left: 32px;
  bottom: 8px;
}

.box-title:after {
  border-left-color: #fff;
}

.type1 {
  height: 197px;
  box-sizing: border-box;
  .typeBar {
    height: 100%;
    border: 2px dashed #7fbcfe;
    background-color: #e5f1fe;
    position: relative;
    img {
      width: 100%;
      height: 100%;
    }
    .imageUrlTitle {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 0;
      z-index: 1;
      background-color: rgba(51, 51, 51, 0.8);
      color: #fff;
      font-size: 14px;
      height: 30px;
      line-height: 30px;
      text-align: left;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      padding: 0 10px;
    }
  }
  &.type1Padding{
    padding: 10px;
  }
  &.type1Margin{
    margin-top: 10px;
    &:first-child{
      margin-top: 0;
    }
  }
}
.type2 /deep/ .el-carousel {
  .el-carousel__indicators {
    .el-carousel__indicator {
      .el-carousel__button {
        width: 6px;
        height: 6px;
        background-color: #333;
        border-radius: 6px;
      }
    }
  }
  .el-carousel__container {
    height: 100%;
    width: 100%;
    img {
      height: 100%;
      width: 100%;
    }
    .lbbtn {
      position: absolute;
      width: 39px;
      height: 18px;
      background: rgba(51, 51, 51, 0.4);
      color: #fff;
      font-size: 12px;
      text-align: center;
      right: 10px;
      bottom: 6px;
      z-index: 999;
    }

    .el-carousel__item {
      // position: relative;
      .imageUrlTitle {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 0;
        z-index: 1;
        background-color: rgba(51, 51, 51, 0.8);
        color: #fff;
        font-size: 14px;
        height: 30px;
        line-height: 30px;
        text-align: left;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding: 0 10px;
      }
    }
  }
}
.type2 {
  ul {
    display: -webkit-box;
    height: 180px;
    box-sizing: border-box;
    overflow: hidden;

    li {
      height: 100%;
      border: 2px dashed #7fbcfe;
      background-color: #e5f1fe;
      width: 340px;
      margin-right: 10px;
      position: relative;
      img {
        width: 100%;
        height: 100%;
      }
      .imageUrlTitle {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 0;
        z-index: 1;
        background-color: rgba(51, 51, 51, 0.8);
        color: #fff;
        font-size: 14px;
        height: 30px;
        line-height: 30px;
        text-align: left;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding: 0 10px;
      }
    }
  }
  .type1 {
    height: 188px;
    box-sizing: border-box;
    position: relative;
    .typeBar {
      height: 100%;
      border: 2px dashed #7fbcfe;
      background-color: #e5f1fe;
    }
    .el-icon-arrow-left,
    .el-icon-arrow-right {
      position: absolute;
      width: 22px;
      height: 22px;
      background: rgba(51, 51, 51, 0.4);
      color: #fff;
      text-align: center;
      line-height: 22px;
      border-radius: 12px;
      top: 50%;
      margin-top: -11px;
    }
    .el-icon-arrow-left {
      left: 22px;
    }
    .el-icon-arrow-right {
      right: 22px;
    }
    .lbbtn {
      position: absolute;
      width: 39px;
      height: 18px;
      background: rgba(51, 51, 51, 0.4);
      color: #fff;
      font-size: 12px;
      text-align: center;
      right: 27px;
      bottom: 26px;
    }
  }
}
</style>
